// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "ProximaNova", sans-serif;
  --fontsemibold-family: "ProximaNovaSemiBold", sans-serif;
  --fontbold-family: "ProximaNovaBold", sans-serif;
  --content-width: 1415px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --cinder-color: #171722;
  --atlantis-color: #84d52c;
  --shark-color: #212129;
  --tuna-color: #30303d;
  --persimmon-color: #ff6d59;
  --dell-color: #297019;
  --lima-color: #7acb22;
  --scorpion-color: #606060;
  --dusty-color: #959595;
  --tuna-gray: #353542;
  --filter: rgb(132 213 44 / 36%);
}
