.range {
  margin-bottom: 15px;

  &__container {
    @include flex-all-sb;
  }

  &__box {
    @include flex-v-center;
  }

  &__prop {
    width: 225px;

    &:not(:last-child) {
      margin-right: 25px;
    }
  }

  &__caption {
    margin-bottom: 20px;
    font-family: var(--fontsemibold-family);
    font-size: 16px;
    line-height: 135%; /* 21.6px */
    color: var(--light-color);
  }

  &__dropdown {
    position: relative;
  }

  &__dropdown-sort {
    cursor: pointer;
  }

  &__dropdown-box {
    position: absolute;
    right: 1%;
    top: 125%;
    overflow: hidden;
    border-radius: 10px;
    max-height: 0;
    box-shadow: 0 10px 25px 0 rgb(15 15 23 / 63%);
    background-color: var(--shark-color);
  }

  &__dropdown-case {
    padding: 20px;
  }

  &__dropdown-title {
    margin-bottom: 10px;
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 135%;
    color: var(--light-color);
  }
}

.item {
  @include flex-v-center;

  width: 180px;
  cursor: pointer;

  &:not(:last-child) {
    margin-bottom: 10px;
  }

  &__icon {
    margin-right: 10px;

    svg {
      fill: var(--atlantis-color);
    }
  }

  &__descr {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 135%;
    color: var(--light-color);
  }
}

.arrow {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    z-index: 2;
    display: block;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.thc-up {
  &::before {
    left: 0.5%;
    top: 5%;
    width: 50px;
    height: 50px;
    background-image: url("../img/up.png");
  }
}

.thc-down {
  &::before {
    left: 40%;
    top: 15%;
    width: 30px;
    height: 30px;
    background-image: url("../img/down.png");
  }
}

.cbd-up {
  &::before {
    left: 0.5%;
    top: 5%;
    width: 50px;
    height: 50px;
    background-image: url("../img/up.png");
  }
}

.cbd-down {
  &::before {
    left: 40%;
    top: 15%;
    width: 30px;
    height: 30px;
    background-image: url("../img/down.png");
  }
}

.range-slider {
  position: relative;
  width: 225px;

  &__value {
    position: relative;
    width: 100%;

    span {
      position: absolute;
      left: 65%;
      top: -33px;
      z-index: 2;
      display: block;
      border-radius: 3px;
      padding: 2px 5px;
      width: 53px;
      font-family: var(--font-family);
      font-size: 16px;
      line-height: 135%;
      color: var(--light-color);
      background-color: var(--tuna-color);
      transform: translateX(-50%) scale(0);
      transform-origin: bottom;
      transition: transform 0.3s ease-in-out;

      &::after {
        content: "";
        position: absolute;
        left: 21px;
        bottom: -4px;
        z-index: -1;
        display: block;
        width: 10px;
        height: 10px;
        background-color: var(--tuna-color);
        rotate: 45deg;
      }
    }
  }

  .show {
    transform: translateX(-50%) scale(1);
  }

  &__input {
    -webkit-appearance: none;
    appearance: none;
    position: relative;
    margin-bottom: 15px;
    width: 100%;
    height: 4px;
    background-color: var(--tuna-color);
  }

  &__put {
    float: right;
  }
}

.span {
  outline: none;
  border: 1px solid var(--scorpion-color);
  border-radius: 3px;
  padding: 1px 5px;
  width: 45px;
  font-family: var(--font-family);
  font-size: 14px;
  line-height: 135%;
  color: var(--dusty-color);
  background-color: var(--shark-color);
}

.range-slider__input::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  border-radius: 100%;
  width: 16px;
  height: 16px;
  box-shadow: 0 3px 15px 0 rgb(132 213 44 / 20%);
  background-color: var(--atlantis-color);
  cursor: pointer;
  pointer-events: auto;
}

.progresBar {
  position: absolute;
  left: 0;
  top: 9.6px;
  width: 65%;
  height: 4px;
  box-shadow: 0 3px 15px 0 rgb(132 213 44 / 20%);
  background-color: var(--atlantis-color);
}

.slider {
  position: relative;
  margin-top: 34px;
  margin-bottom: 15px;
  width: 100%;
  height: 4px;
  background-color: var(--tuna-color);

  .progres {
    position: absolute;
    left: 0;
    right: 40%;
    height: 4px;
    box-shadow: 0 3px 15px 0 rgb(132 213 44 / 20%);
    background-color: var(--atlantis-color);
    cursor: pointer;
    pointer-events: auto;
  }

  input {
    -webkit-appearance: none;
    appearance: none;
    position: absolute;
    top: 50%;
    width: 100%;
    background: none;
    transform: translateY(-50%);
    pointer-events: none;
  }

  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: none;
    border-radius: 100%;
    width: 16px;
    height: 16px;
    box-shadow: 0 3px 15px 0 rgb(132 213 44 / 20%);
    background-color: var(--atlantis-color);
    cursor: pointer;
    pointer-events: auto;
  }

  input[type="range"]::-moz-range-thumb {
    -moz-appearance: none;
    height: 4px;
    box-shadow: 0 3px 15px 0 rgb(132 213 44 / 20%);
    background-color: var(--atlantis-color);
    cursor: pointer;
    pointer-events: auto;
  }
}

.slider-track {
  position: absolute;
  height: 100%;
  box-shadow: 0 3px 15px 0 rgb(132 213 44 / 20%);
  background-color: var(--atlantis-color);
}

.tooltip {
  position: absolute;
  display: block;
  border-radius: 3px;
  padding: 2px 5px;
  width: 53px;
  font-family: var(--font-family);
  font-size: 16px;
  line-height: 135%;
  color: var(--light-color);
  background-color: var(--tuna-color);

  &::after {
    content: "";
    position: absolute;
    left: 21px;
    bottom: -4px;
    z-index: -1;
    display: block;
    width: 10px;
    height: 10px;
    background-color: var(--tuna-color);
    rotate: 45deg;
  }
}

.min-tooltip {
  left: 50%;
  top: -45px;
  z-index: 5;
  transform: translateX(-50%) scale(0);
  transform-origin: bottom;
  transition: transform 0.3s ease-in-out;
}

.max-tooltip {
  right: 50%;
  top: -45px;
  z-index: 5;
  transform: translateX(-50%) scale(0);
  transform-origin: bottom;
  transition: transform 0.3s ease-in-out;
}

.input-box {
  @include flex-all-sb;
}

.input-wrap {
  @include flex-v-center;

  position: relative;
  border: 1px solid var(--scorpion-color);
  border-radius: 3px;
  padding: 1px 5px;
  background-color: var(--shark-color);
}

.input-field {
  outline: none;
  width: 28px;
  font-family: var(--font-family);
  font-size: 13px;
  line-height: 135%;
  color: var(--dusty-color);
}

.input-addon {
  font-family: var(--font-family);
  font-size: 13px;
  line-height: 135%;
  color: var(--dusty-color);
}
