.search {
  margin: 20px 0;

  &__container {
    @include flex-v-center;
  }

  &__title {
    font-family: var(--fontbold-family, sans-serif);
    font-size: 30px;
    line-height: 30px; /* 100% */
    letter-spacing: 0.3px;
    cursor: pointer;
  }

  &__bar {
    @include flex-v-center;

    margin: 5px 16px 0;
    border: 1px solid var(--dusty-color);
    border-radius: 12px;
    padding: 12px 124px 12px 20px;
  }

  &__button {
    svg {
      fill: var(--dusty-color);
    }
  }

  &__input {
    margin-left: 5px;
    outline: none;
    width: 300px;
    font-size: 16px;
    line-height: 135%; /* 21.6px */
    letter-spacing: 0.16px;
    text-transform: capitalize;
    color: var(--dusty-color);

    &::placeholder {
      font-family: var(--fontsemibold-family, sans-serif);
      font-size: 16px;
      line-height: 135%; /* 21.6px */
      letter-spacing: 0.16px;
      text-transform: capitalize;
      color: var(--dusty-color);
    }
  }

  &__checkbox {
    @include flex-v-center;
  }
}
