.dropdown {
  margin-bottom: 148px;

  &__box {
    @include flex-v-center;

    margin-bottom: 30px;
  }
}

.breadcrumb {
  @include flex-v-center;

  &__title {
    margin-right: 30px;
    font-family: var(--fontsemibold-family);
    font-size: 16px;
    line-height: 135%;
    color: var(--light-color);
  }

  &__item {
    @include flex-v-center;

    margin-right: 10px;
    border-radius: 20px;
    padding: 5px 8px;
    box-shadow: -5px -5px 16px 0 rgb(138 138 138 / 9%) inset;
    background: var(--dell-color);
    cursor: pointer;

    span {
      margin-right: 5px;
      font-family: var(--font-family);
      font-size: 13px;
      line-height: 135%;
      color: var(--light-color);
    }

    svg {
      width: 18px;
      height: 18px;
    }

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__btn {
    margin-top: 3px;
    pointer-events: none;
  }
}

.sidebar {
  position: relative;
  overflow: hidden;
  border: 1px solid rgb(132 213 44 / 36%);
  border-radius: 10px;
  padding: 15px 22px 30px 17px;
  width: 243px;
  background-color: var(--tuna-gray);

  &::before {
    content: "";
    position: absolute;
    left: -43%;
    top: 36%;
    display: block;
    width: 207px;
    height: 207px;
    background-image: url("../img/leaf.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__title {
    margin-bottom: 10px;
    font-family: var(--fontsemibold-family);
    font-size: 18px;
    line-height: 135%;
    color: var(--light-color);
  }

  &__item {
    width: 204px;
  }

  &__item-box {
    @include flex-all-sb;

    cursor: pointer;
    user-select: none;

    svg {
      margin-bottom: 5px;
      width: 12px;
      height: 12px;
      fill: var(--dusty-color);
      transition: transform 0.3s ease-in-out;
    }
  }

  &__checkbox-box {
    overflow: hidden;
    max-height: 0;
    transition: max-height 0.3s ease-in-out;
  }

  &__checkbox-item {
    position: relative;
    margin-bottom: 15px;

    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }
}

.active {
  max-height: 1000px;
  transition: max-height 0.9s ease-in-out;
}

.custom-checkbox {
  span {
    margin-left: 25px;
    font-family: var(--fontsemibold-family);
    font-size: 16px;
    line-height: 147%;
    color: var(--dusty-color);
    transition: color 0.2s ease-in-out;
    cursor: pointer;
    user-select: none;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 3px;
      display: block;
      border: 1px solid var(--dusty-color);
      border-radius: 4px;
      width: 15px;
      height: 15px;
      background-color: var(--tuna-gray);
      transition: background-color 0.3s ease-in-out, border 0.3s ease-in-out;
    }
  }

  input[type="checkbox"]:checked + span {
    color: var(--light-color);
    transition: color 0.2s ease-in-out;
  }

  input[type="checkbox"]:checked + span::before {
    border: none;
    background-color: var(--atlantis-color);
    transition: background-color 0.3s ease-in-out, border 0.3s ease-in-out;
  }

  input[type="checkbox"]:checked + span::after {
    content: "";
    position: absolute;
    left: 0.5%;
    top: 17%;
    z-index: 2;
    display: block;
    width: 12px;
    height: 12px;
    background-image: url("../img/check.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    transition: background-image 0.3s ease-in-out;
  }
}

.opened {
  transform: rotate(180deg);
}
